import React, { useEffect, useState } from "react";
import Navbar from "../../components/Navbar";
import {useDispatch,useSelector} from "react-redux"
import axios from "axios";
import SearchPage from "./SearchPage";
import { SearchApi } from "../../redux/actions";
import { Redirect, useLocation } from "react-router-dom";
import { connect } from "react-redux";
function SearchPageData({ loading, error, unauthorized, SearchApi }) {
   const dispatch=useDispatch();
  const dataFromAPI=useSelector(state=>state.reducer)

  const searchParams = new URLSearchParams(document.location.search);
  const [resultsObtained, setResultsObtained] = useState([]);
  const userid = localStorage.getItem("UserId");
  console.log(userid)
  const searchTerm = searchParams.get("SearchTerm");
  
  useEffect( () => {
    //  dispatch(SearchApi(searchTerm));
    axios.post("https://fhnsgxnpa9.execute-api.us-east-1.amazonaws.com/v1/findrelevantdocument",{SearchTerm:searchTerm,UserId:userid})
    .then((response)=>{
      console.log(response.data.response);
      if (response.status==200){
        const SearchResults=response.data;
        const set=SearchResults.json();
        setResultsObtained(set)
        console.log(set);
        console.log(SearchResults);
        console.log(resultsObtained)
      }
      
    })
    .catch((response)=>{
      if (response.satusCode==500){
        console.log(error)
      }
    })
  }, []);
  useEffect(()=>{
    console.log(resultsObtained)
  })



  return (
  <>{searchTerm !== "" ? <SearchPage resultsObtained={resultsObtained} /> : <Redirect to={"/home"} />}</>)
}
const mapStateToProps = (state) => ({
  loading: state.loading,
  data: state.data,
  error: state.error,
});

const mapDispatchToProps = {
  SearchApi,
};
export default connect(mapStateToProps, mapDispatchToProps)(SearchPageData);
