import React, { useState, useEffect } from "react";
import axios from "axios";
import "./profile-data.css";
import { useHistory } from "react-router-dom";
import AddProfilePic from "./fileUpload/addProfilePic";
import RemoveProfilePic from "./fileDelete/removeProfilePic";
import {UpdateUserAPI} from "./thunk"
import { connect } from 'react-redux';
const ProfileData = ({loading, error, unauthorized, UpdateUserAPI}) => {
  const history = useHistory();
  var [name, setName] = useState();
  var [email, setEmail] = useState();
  var [mobileNumber, setMobileNumber] = useState();
  const [userId, setUserID] = useState();
  var [imageUrl, setImageUrl] = useState(
    "data:image/png;base64,"
  );
  const [data, setData] = useState(null);
  const [ProfileImage, setProfielImage] = useState();
  const [openAddProfilePic, setopenAddProfilePic] = useState(false);
  const [openRemoveProfilePic, setopenRemoveProfilePic] = useState(false);
  // console.log(profileImage);
  useEffect(() => {
    const id = localStorage.getItem("UserId");
    setUserID(id);
  }, []);

  useEffect(() => {

    if (userId !== undefined) {
      axios
        .post(
          "https://fhnsgxnpa9.execute-api.us-east-1.amazonaws.com/v1/userdetails",
          { UserId: userId }
        )
        .then((result) => {
          console.log(result)
          setData(result.data.response);
          setName(result.data.response.Name);
          setEmail(result.data.response.Email);
          setMobileNumber(result.data.response.MobileNo);
          if (result.data.response.ProfileImageUrl) {
            setImageUrl(result.data.response.ProfileImageUrl);
          }
        });
    }
  }, [userId]);

  function handleSubmit(e) {
    e.preventDefault();
    UpdateUserAPI(name, email, mobileNumber, userId);
    
  }

  return (
    <>
      <div className="profile">
        <div className="profile-pic">
          <img className="Profile-Avatar" src={imageUrl} alt="Avatr" />
          <div className="profile-pic-options">
            <a className="edit-pic" onClick={() => setopenAddProfilePic(true)}>
              Edit profile photo
            </a>
            <br />
            <a
              className="remove-pic"
              onClick={() => setopenRemoveProfilePic(true)}
            >
              Remove Profile photo
            </a>
            <br />
          </div>
        </div>
        <form onSubmit={handleSubmit}>
          <div className="profile-details">
            <div className="row-data">
              <label for="profile-name" className="profile-headings">
                Full Name
              </label>
              <input
                type="text"
                contenteditable="true"
                className="profile-name profile-values"
                onChange={(event) => setName(event.target.value)}
                id="profile-name"
                value={name}
              ></input>
            </div>
            <div className="row-data">
              <label className="profile-headings">Email</label>
              <input
                type="text"
                contenteditable="true"
                className="profile-email profile-values"
                onChange={(event) => setEmail(event.target.value)}
                value={email}
              ></input>
            </div>
            <div className="row-data">
              <label className="profile-headings">Phone Number</label>
              <input
                type="text"
                contenteditable="true"
                className="profile-number profile-values"
                onChange={(event) => setMobileNumber(event.target.value)}
                value={mobileNumber}
              ></input>
            </div>
          </div>
          <div className="user-credentials">
            <div className="row-creds">
              <label className="creds-site">Medium Username</label>
              <input
                type="text"
                contentEditable="true"
                className="medium-username creds-values"
              ></input>
              <label className="creds-site">Medium Password</label>
              <input
                type="text"
                contentEditable="true"
                className="medium-password creds-values"
              ></input>
            </div>
            <div className="row-creds">
              <label className="creds-site">Quora Username</label>
              <input
                type="text"
                contentEditable="true"
                className="medium-username creds-values"
              ></input>
              <label className="creds-site">Quora Password</label>
              <input
                type="text"
                contentEditable="true"
                className="medium-password creds-values"
              ></input>
            </div>
          </div>

          <div>
            <button className="profile-submit">Submit</button>
          </div>
        </form>
        <AddProfilePic
          open={openAddProfilePic}
          onClose={() => setopenAddProfilePic(false)}
        />
        <RemoveProfilePic
          open={openRemoveProfilePic}
          onClose={() => setopenRemoveProfilePic(false)}
        />
      </div>
    </>
  );
};

  const mapStateToProps = (state) => ({
  loading: state.loading,
  data: state.data,
  error: state.error,
});

const mapDispatchToProps = {
  UpdateUserAPI,
};
export default connect(mapStateToProps, mapDispatchToProps) (ProfileData);
